html {
  font-family: Arial, Helvetica Neue, Helvetica, sans-serif;
  font-size: 20px;
  font-weight: 700;
}

.people {
  grid-template-columns: repeat(2, 200px);
  justify-content: center;
  gap: 40px 30px;
  padding-inline: 35px;
  display: grid;
}

@media (width >= 700px) {
  .people {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 200px);
  }
}

.people__person {
  box-sizing: border-box;
  border-radius: 10px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10px;
  transition-duration: 1s;
  display: flex;
}

.people__person:hover {
  background-color: #eee;
  transition-duration: 0s;
  transform: scale(1.2);
}

.people__image {
  border-radius: 50%;
  width: 200px;
  height: 200px;
  transition-duration: 1s;
}

.people__image:hover {
  border-radius: 40px;
  transition-duration: 0s;
}

.people__photo {
  position: relative;
}

.people__rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  display: flex;
  position: absolute;
  bottom: 45%;
  right: 10px;
}

.people__extra {
  display: none;
}

.people__extra:hover {
  display: inline;
}
